import { Interceptor, KSBaseResponse } from '@locallife/base-request';

export interface InterceptorParams {
    customInnerRequestInterceptors?: Array<Interceptor>; // 请求拦截器
    customInnerResponseInterceptors?: Array<Interceptor>; // 响应拦截器，eg：自定义错误处理逻辑、登录状态失效逻辑
    enableRequestParamsVerify?: boolean; // 是否开启请求参数校验
    requestVerifyParams?: Map<string, string>; // 参数校验列表
    enableResponseBaseVerify?: boolean; // 是否开启返回状态参数校验
    responseBaseVerifyFunc?: (response: KSBaseResponse) => boolean; // 返回状态参数校验，自定义校验规则： true —— 校验成功，false —— 校验失败
    disableLogTimeCost?: boolean; // 是否关闭耗时统计上报
    disableReportMonitorLog?: boolean; // 是否关闭上报接口监控日志
}
